
import {url, get, post} from "@/utils/ApiDecorator"

import DoApi from '../service/DoApi'

class User {
  // 用户列表
  @url("/classes/_User")
  @get
  list(params) {}

  // cloudQuery
  @url("/cloudQuery")
  @get
  cloudQuery(params) {}

  // userInfo
  userInfo(params) {
    return $axios.get("/users/" + params.objectId + '?' + DoApi.jsonUrlFormat(params))
  }

}

export default new User()
