/**
 * 数据处理
 */
class DoData {

  constructor() {

  }

  /**
   * 查看对象数组中的属性，返回index
   * @param list [] 源对象数据
   * @param value string 要判断相等的值
   * @param type string 要判断相等的属性值
   * return 查到返回index,否则返回-1
   */
  searchArrayIndex(list, value, type) {
    let searchIndex = -1
    if(!list) return searchIndex
    list.forEach((item, index) => {
      if(item[type] == value) searchIndex = index
    })
    return searchIndex
  }


  /**
   * 获取用户角色
   * roles 角色数组
   */
  getUserRole() {
    // 默认返回 第一个角色
    let userInfo = JSON.parse(sessionStorage.userInfo)
    let roles = userInfo.roles
    if(roles)  return roles[0]
    else return ''
  }
  // 是否能使用筛选
  canFilter() {
    let role =  this.getUserRole()
    if(role && role.realId && (role.realId ==6 || role.realId == 7)) return false
    return true
  }

  /**
   * 获取用户年级
   */
  getUserClass() {
    // 默认返回 第一个年级
    let roles = this.getUserRole()
    if(roles.id == 1) {
      return wx.getStorageSync('studentClass')?wx.getStorageSync('studentClass')[0]:{}
    } else if(roles.id == 2) {
      return wx.getStorageSync('teacherClass')?wx.getStorageSync('teacherClass')[0]:{}
    }

  }

  /**
   * 传入对象数组，返回需的 属性值 数组
   */
  doIds(list, props) {
    if(!list) return []
    let tmp = []
    list.forEach((item, index) => {
      if(item[props]) tmp.push(item[props])
    })
    return tmp
  }

   /**
   * 时间转换，秒转到 时分秒
   */
  convertSeconds(seconds) {
    if(!seconds) return seconds
    let hours = parseInt(seconds/3600)
    let minutes = parseInt(seconds/60)
    let secs = seconds%60

    let time = ''
    if(hours > 0) {
      time = hours + "时" + minutes + "分" + secs + "秒"
    } else if(minutes > 0) {
      time = minutes + "分" + secs + "秒"
    } else {
      time = secs + "秒"
    }
    return time
  }

 /**
   * 段落文字 处理
   * <center></center>为古诗，需要换行处理
  */
  doPara(list) {
    if(list.length == 0 || !list ) return list

    list.forEach((item, index) => {
      if(/center/.test(item.name)) {
        item.name = item.name.replace('<center>', '<p style="text-align:center">').replace('</center>', '</p>').replace(/\n/gi, '<br>')

        // item.name = item.name.replace(/\s{2,}/g, '<br>')
        list[index].name = "<p style='line-height: 23px; text-index: 0;'>" + item.name + "</p>"
        list[index].isRichText = true

      } else {
        list[index].isRichText = false
      }

      // console.log("text:", item.name)

    })

    return list
  }

  // 倒计时
  convertDownDate(value) {
    if(!value) return ''
    let currentDate = new Date().getTime()
    let targetDate =  new Date(value.replace(/-/g,"/")).getTime()

    let difftime= parseInt((targetDate - currentDate) / 1000);

    let days = Math.floor(difftime/24/3600)
    // let hours = parseInt(difftime/3600 - days*24)
    // let mins = parseInt(difftime/60 - days*24*60 - hours*60)
    // let secs = parseInt(difftime- days*24*3600 - hours*3600 - mins*60)

    if(difftime < 0) return '已过期'+ Math.abs(days) + '天'

    return '距离截止日期' + days + '天'
    // + hours + '时'  + mins + '分' + secs + '秒'

  }

  // 倒计时
  convertDownDateNum(value) {
    if(!value) return ''
    let currentDate = new Date().getTime()
    let targetDate =  new Date(value.replace(/-/g,"/")).getTime()
    let difftime= parseInt((targetDate - currentDate) / 1000);
    let days = Math.floor(difftime/24/3600)
    return days
  }

  // 默认头像转换
  doAvatar(roleId, sex) {
    if(!roleId) return ''
    if(roleId == 1) {
      if(sex === 0 ) {
        return "/img/avatar/stu-girl.png"
      } else {
        return "/img/avatar/stu-boy.png"
      }
    } else {
      if(sex === 0 ) {
        return "/img/avatar/tea-woman.png"
      } else {
        return "/img/avatar/tea-man.png"
      }
    }
  }

  // 角色切换
  changeRole(roles) {
    let itemList = []
    roles.forEach((item, index)=>{
      if(item.id == 2) {
        itemList.push('切换为 教师')
      }else if(item.id == 4 || item.id == 3) {
        itemList.push('切换为 校长或年级组长')
      } else if(item.id == 5) {
        itemList.push('切换为 区管理者')
      }

    })
    wx.showActionSheet({
      itemList: itemList,
      success: (res) => {
        let userInfo = wx.getStorageSync('userInfo')
        let tmp = roles[0]
        roles[0] = roles[res.tapIndex]
        roles[res.tapIndex] = tmp
        userInfo.roles = roles
        wx.setStorageSync('userInfo', userInfo)
        wx.reLaunch({
          url: '/pages/splash/splash'
        })

      },
      fail: function(res) {
        console.log(res.errMsg)
      }
    })

  }

  /**
   * 处理word task list
   * 循环hardids softids,组装数据
   */
  doWordTask(data) {

    var taskList = []

    data.forEach((item, index) => {
      let hardIds = item.hardids.split(',')
      let softIds = item.softids.split(',')

      hardIds.forEach((hardItem) => {
        let itemData = this.findArrayByProps(item.testchars, 'id', hardItem)
        this.doWordList(itemData, item, taskList, 'hard')
      })

      softIds.forEach((softItem) => {
        let itemData = this.findArrayByProps(item.testchars, 'id', softItem)
        this.doWordList(itemData, item, taskList, 'soft')
      })
    })
    return taskList
  }

  doWordList(itemData, item, taskList,type) {
    if(itemData == -1) {
      return taskList
    }
    let taskItem = {}
    taskItem.taskId = item.id
    taskItem.endTime = ''
    taskItem.endTimeNum = ''
    taskItem.wordType = type
    taskItem = Object.assign(taskItem, item, itemData)
    taskList.push(taskItem)
    return taskList

  }

  findArrayByProps(data, props,targetValue) {
    let sourceIndex = -1
    data.forEach((item, index) => {
      if(item[props] == targetValue ) {
        sourceIndex = index
      }
    })
    if(sourceIndex == -1) return sourceIndex
    return data[sourceIndex]
  }

}

// 导出单例，方便直接使用
export default new DoData()
