import Vue from 'vue'
import VueRouter from 'vue-router'
import Router from './pages/Router.vue'

import Login from './pages/login/login.vue'
import Home from './pages/home/home.vue'
import UserDetails from './pages/user/user-details.vue'
import News from './pages/news/news.vue'
import NewsDetails from './pages/news/news-details.vue'

Vue.use(VueRouter)
export default new VueRouter({
  routes: [
    {
      path: '/',
      name: 'router',
      redirect: '/home'
    },
    {
      path: '/login',
      name: '登录',
      component: Login
    },
    {
      path: '/home',
      name: '首页',
      component: Home
    },
    {
      path: '/user-details',
      name: '用户详情',
      component: UserDetails
    },
    {
      path: '/news',
      name: '资讯传递',
      component: News
    },
    {
      path: '/news-details',
      name: '资讯详情',
      component: NewsDetails
    },
  ]
})
