      <div class="info-header">课程相关</div>
      <div class="info-item info-item-inline" v-if="userInfo.teacher_type == 2 || userInfo.teacher_type == 3">
        <span class="info-name">任教学科：</span>
        <span class="info-content">{{course.subject || '暂无内容'}}</span>
      </div>

      <div class="info-item info-item-inline" v-if="userInfo.teacher_type != 1">
        <span class="info-name" >主讲课程：</span>
        <span class="info-content">{{course.coursename || '暂无内容'}}</span>
      </div>

      <div class="info-item info-item-inline" v-if="userInfo.teacher_type == 1">
