<style lang="less">
  @import './less/user.less';
  @import 'user-details.less';
</style>

<script>
  // import card from '@/components/card'

  import User from '@/api/User'

  export default {
    data () {
      return {
        Config: this.Config,
        userInfo: {},
        course: {},
        award: {},
        education: {},
        job: {},
      }
    },

    components: {
      // card
    },
    onHide() {
    },

    onShow() {
      this.retsetData()
    },

    methods: {

      retsetData() {
        this.userInfo = {}
        this.course = {}
        this.award = {}
        this.education = {}
        this.job = {}
      },

      doQuery(db) {
        return encodeURIComponent(`select * from ${db} where user_objectid='` + (this.$route.query.objectId || '5c74845a12215f005362e7c3') + "'")
      },

      async getUserInfo() {
        let params = {
          objectId: this.$route.query.objectId || '5c8e097912215f0072bb41b6'
        }
        this.userInfo = await User.userInfo(params)
        console.log(this.userInfo)
      },

      async getCourseInfo() {
        let params = {
          cql: this.doQuery('course')
        }
        let resData = await User.cloudQuery(params)
        if(resData.results && resData.results.length > 0) {
          this.course = resData.results[0]
        }
      },

      async getAwardInfo() {
        let params = {
          cql: this.doQuery('award')
        }
        let resData = await User.cloudQuery(params)
        if(resData.results && resData.results.length > 0) {
          this.award = resData.results[0]
        }
      },

      async getEducationInfo() {
        let params = {
          cql: this.doQuery('education')
        }
        let resData = await User.cloudQuery(params)
        if(resData.results && resData.results.length > 0) {
          this.education = resData.results[0]
        }
      },

      async getJobInfo() {
        let params = {
          cql: this.doQuery('job')
        }
        let resData = await User.cloudQuery(params)
        if(resData.results && resData.results.length > 0) {
          this.job = resData.results[0]
        }
      },

      goToEdit() {
        this.$router.replace({
          path: '/pages/user/edit/user-edit',
          query: {
            objectId: this.$route.query.objectId || '5c8e097912215f0072bb41b6',
          }
        })
      }
    },

    created() {

    },

    mounted () {
      this.getUserInfo()
      this.getCourseInfo()
      this.getAwardInfo()
      this.getEducationInfo()
      this.getJobInfo()
    },
  }
</script>

