import User from '@/api/User'
import DoData from '@/service/DoData'
import {Toast} from 'mint-ui'
export default {
  data() {
    return {
      username: '',
      password: '',
      openId: 'test',
      showPwd: true
    }
  },

  components: {},
  mounted() {
    let userInfo =sessionStorage.userInfo
    if (!!userInfo) {
      userInfo = JSON.parse(userInfo)
      this.username = userInfo.phone
      this.password = sessionStorage.pwd
    }
  },

  methods: {
    showPwdCtrl() {
      this.showPwd = !this.showPwd
    },

    async doLogin() {
      this.username = this.username.trim()
      if (!this.username) {
        Toast("手机号不能为空")
        return false
      }
      this.password = this.password.trim()
      if (!this.password) {
        Toast("密码不能为空")
        return false
      }

      let param = {
        app_key: this.Config.key.app_key,
        secret_key: this.Config.key.secret_key,
        phone: this.username,
        password: this.password,
        type: 1,
        open_id: this.openId
      }
      let resData = await User.login(param)
      if (resData.code == 400) {
        Toast("用户或密码错误")
        return false
      }
      sessionStorage.token = resData.token
      this.getUserInfo(resData.roles)
    },

    async getUserInfo(roles) {
      let userData = await User.getUserInfo({})

      // 大赛用户权限处理 6 大赛学生，7 大赛老师
      if(roles[0].id == 6) {
        roles[0].id = 1
        roles[0].realId = 6
      } else if(roles[0].id == 7) {
        roles[0].id = 2
        roles[0].realId = 7
      }
      userData.roles = roles
      userData.pwd = this.password
      console.log("userInfo:", userData)
      sessionStorage.userInfo = JSON.stringify(userData)
      this.goToHome(userData)
    },

    async goToHome(userData) {
      let userRole = DoData.getUserRole()
      Toast('欢迎 ' + userData.name)

      setTimeout(() => {
        this.$router.replace({path: 'exercise-activity', query: {bid: 6}})
      }, 2000)
    }
  }
}
