import User from '@/api/User'
export default {
  data() {
    return {
      userList: []
    }
  },

  components: {},

  methods: {
    async getUserList() {
      let resData = await User.list()
      console.log(resData)
      if(resData.results) {
        this.userList = resData.results
      }
    },

    goToDetails(item) {
      this.$router.push({path: '/user-details', query: {objectId: item.objectId}})
    },

    goToLink(path) {
      this.$router.push({path: '/news'})
    }

  },

  mounted() {
    this.getUserList()
  },
}
